















import { createComponent, onMounted, reactive, ref, computed, watch } from "@vue/composition-api";
import Api from "../api";

export default createComponent({
  setup() {
    let username = ref("");
    let password = ref("");

    let login = async function() {
      let result = await Api.auth(username.value, password.value);
    };
    return {
      username,
      password,
      login
    };
  }
});
